import React, { useState } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGripHorizontal  } from '@fortawesome/free-solid-svg-icons'
import { faGripVertical } from '@fortawesome/free-solid-svg-icons'

import {  NavLink } from "react-router-dom";


const Navbar = () => {
   
    return (
        <>
       <div className="upper-nav-container">
      <div className="upper-nav"> <NavLink  to = "/horizontal" className="toggel-link"><FontAwesomeIcon className="toggle-button" icon={ faGripHorizontal } /></NavLink></div>
      <div className="upper-nav">  <NavLink to = "/vertical"  className="toggel-link"><FontAwesomeIcon className="toggle-button" icon={ faGripVertical} /></NavLink></div>
      <div className="upper-nav">     <NavLink to ="/form">  <button className="feedback-upper">Feedback</button></NavLink></div>
               </div>
       <nav className="nav-container">
           <div className="nav">
               <img className="profile" src="data:image/jpeg;base64,"/>
               <h3 className="heading">Hi Readers,</h3>
               <p className="para">Here's your news!</p>
           </div>
           <div className="nav">
               <h3 className="heading1">View Toggle</h3>
               <NavLink  to = "/horizontal" className="toggel-link"><FontAwesomeIcon className="toggle-button" icon={ faGripHorizontal } /></NavLink>
               <NavLink to = "/vertical"  className="toggel-link"><FontAwesomeIcon className="toggle-button" icon={ faGripVertical} /></NavLink>
               
           </div>
            <div className="nav">
                <h3 className="heading1">Have a Feedback? </h3>
              <NavLink to ="/form">  <button className="feedback-btn">We're Listening!</button></NavLink>
                
            </div>
       </nav>
       
       
      
        </>
    )
}

export default Navbar
